import greatCircle from '@turf/great-circle';
import { point } from '@turf/helpers';
import { createStore } from 'vuex';

const checkup = (state) => {
  const data = {
    type: 'FeatureCollection',
    features: [],
  };

  console.log('checkup', state.routes);
  state?.routes
    .filter((r) => r?.from?.coordinates?.length && r?.to?.coordinates?.length)
    .forEach((r) => {
      const route = greatCircle(point([...r.from?.coordinates].reverse()), point([...r.to?.coordinates].reverse()));

      data.features.push(route);
    });

  state.map.getSource('routes').setData(data);
};

export const store = createStore({
  state() {
    return {
      map: null,
      routes: [
        {
          from: null,
          to: null,
        },
      ],
    };
  },
  getters: {
    routes(state) {
      return state.routes;
    },
  },
  mutations: {
    initMap(state, map) {
      state.map = map;
    },
    addItem(state) {
      state.routes.push({
        from: null,
        to: null,
      });
      checkup(state);
    },
    removeItem(state, index) {
      state.routes.splice(index, 1);
      checkup(state);
    },
    setFrom(state, { from, index }) {
      console.log('setFrom', from, index);
      state.routes[index] = {
        ...state.routes[index],
        from,
      };

      checkup(state);
    },
    setTo(state, { to, index }) {
      state.routes[index] = {
        ...state.routes[index],
        to,
      };

      checkup(state);
    },
  },
});
