<template>
  <div class="search">
    <v-autocomplete v-model="from" :items="items" return-object label="From" item-title="code" variant="solo-filled"
      density="comfortable">
      <template v-slot:item="{ props, item }">
        <v-list-item v-bind="props" :subtitle="item.raw.name" :title="item.raw.code"></v-list-item>
      </template>
    </v-autocomplete>

    <v-autocomplete v-model="to" :items="items" return-object label="To" item-title="code" variant="solo-filled"
      density="comfortable">
      <template v-slot:item="{ props, item }">
        <v-list-item v-bind="props" :subtitle="item.raw.name" :title="item.raw.code"></v-list-item>
      </template>
    </v-autocomplete>

    <v-btn class="delete" size="x-small" @click="removeItem">X</v-btn>

  </div>
</template>

<script>
export default {
  name: 'RouteComponent'
}
</script>

<script setup>
import { store } from '@/store/store';
import { defineProps, ref, watch } from 'vue';
import { items } from '../assets/data';

const properties = defineProps(['index']);

const route = store.getters.routes[properties.index];
let from = ref(route.from);
let to = ref(route.to);

function removeItem() {
  store.commit('removeItem', properties.index);
}

watch(from, async (value) => {
  if (!value) return;

  console.log(' watch from', properties.index)
  store.commit('setFrom', { from: value, index: properties.index });
})

watch(to, async (value) => {
  if (!value) return;

  store.commit('setTo', { to: value, index: properties.index });
})
</script>


<style lang="scss" scoped>
.search {
  display: flex;
  flex-flow: row nowrap;
  gap: 8px;
  height: 48px;

  .v-input {
    min-width: 140px;
  }

  .delete {
    height: 48px;
  }
}
</style>